import React from 'react';
import './App.css';
// import axios from 'axios';
import { Configuration, OpenAIApi } from "openai";

enum Author {
  HUMAN='Human',
  ROBOT='Robot',
}

interface Message {
  text: string;
  author: Author;
}

const initialMessages: Message[] = [
  { text: 'Hello, I am a helpful chatbot eager to please', author: Author.ROBOT },
];

interface ChatState {
  messages: Message[];
  robotThinking: boolean;
}

enum ChatActionType {
  ADD_MESSAGE,
  ROBOT_RESPONSE_REQUESTED,
}

interface NewMessage {
  type: ChatActionType.ADD_MESSAGE;
  payload: Message;
}

interface RobotResponseRequested {
  type: ChatActionType.ROBOT_RESPONSE_REQUESTED;
  payload: null;
}

type ChatAction = NewMessage | RobotResponseRequested;

const getRobotResponse = async (messages: Message[], engine: string, organization: string, apiKey: string) => {
  const prompt: string = (
    'Below is a conversation between a helpful chatbot and a human.\n\n' + 
    messages.map(m => `${m.author === Author.ROBOT ? 'Robot' : 'Human'}: ${m.text}`).join('\n') + 
    '\n\nWrite what the robot says next.\n\nRobot:'
  )
  const configuration = new Configuration({organization, apiKey});
  const openai = new OpenAIApi(configuration);
  // console.log('Using this prompt:', prompt);
  const response: any = await openai.createCompletion({
    model: engine,
    prompt: prompt,
    temperature: 0.7,
    stop: "\n",
    max_tokens: 100,
  });
  // console.log('Got response:', response);
  const text: string = response.data.choices[0].text;
  // const text: string = await axios.post('http://localhost:3001/chat', {messages, engine}).then(res => res.data);
  return {
    text: text.trim() || '[The robot did not say anything]',
    author: Author.ROBOT,
  }
}

const chatReducer = (state: ChatState, action: ChatAction) => {
  switch (action.type) {
    case ChatActionType.ADD_MESSAGE:
      return {
        ...state,
        messages: [...state.messages, action.payload],
        robotThinking: false,
      };
    case ChatActionType.ROBOT_RESPONSE_REQUESTED:
      return {
        ...state,
        robotThinking: true,
      };
    default:
      return state;
  }
}

function App() {
  const [chat, dispatchChat] = React.useReducer(chatReducer, {
    messages: initialMessages,
    robotThinking: false,
  });
  const [engine, setEngine] = React.useState<string>('code-davinci-002');
  const [organization, setOrganization] = React.useState<string>('');
  const [apiKey, setApiKey] = React.useState<string>('');
  const [showCredentials, setShowCredentials] = React.useState<boolean>(false);

  const Credentials = () => (
    <div className="Credentials">
      <div className="Organization:&nbsp;">
        <label>Organization</label>
        <input value={organization} onChange={e => setOrganization(e.target.value)} />
      </div>
      <div className="ApiKey">
        <label>API Key:&nbsp;</label>
        <input value={apiKey} onChange={e => setApiKey(e.target.value)} />
      </div>
    </div>
  );

  // Chat where you can type in organization and api key and then hide them from view
  return (
    <div className="App">
      <div className="Title">
        <h1>Code chat</h1>
      </div>
      <div className="ShowHideCredentials">
        <button onClick={() => setShowCredentials(!showCredentials)}>
          {showCredentials ? 'Hide credentials' : 'Show credentials'}
        </button>
      </div>
      {showCredentials && (
        <div className="Credentials">
          <div className="Organization:&nbsp;">
            <label>Organization</label>
            <input value={organization} onChange={e => setOrganization(e.target.value)} />
          </div>
          <div className="ApiKey">
            <label>API Key:&nbsp;</label>
            <input value={apiKey} onChange={e => setApiKey(e.target.value)} />
          </div>
        </div>
      )}
      <div className="EngineSelector">
        <label>Engine:&nbsp;</label>
        <select value={engine} onChange={e => setEngine(e.target.value)}>
          <option value="code-davinci-002">Code (free)</option>
          <option value="text-davinci-002">Text (money)</option>
        </select>
      </div>
      <div className="ChatContainer">
        <Chat messages={chat.messages} robotThinking={chat.robotThinking} />
      </div>
      <div className="InputContainer">
        <UserInput dispatchChat={dispatchChat} chat={chat} engine={engine} organiztion={organization} apiKey={apiKey} />
      </div>
    </div>
  );
}

interface ChatProps {
  messages: Message[];
  robotThinking: boolean;
}

const Chat = ({ messages, robotThinking }: ChatProps) => {
  return (
    <div className="Chat">
      {messages.map((message, index) => {
        return (
          <div key={index}>
            {message.author === Author.HUMAN ? (
              <div className="HumanMessage">
                <div className="HumanMessageText">{message.text}</div>
              </div>
            ) : (
              <div className="RobotMessage">
                <div className="RobotMessageText">{message.text}</div>
              </div>
            )}
          </div>
        );
      })}
      {robotThinking ? (
        <div className="RobotThinking">
          <div className="RobotThinkingText">Robot is thinking...</div>
        </div>
      ) : null}
    </div>
  );
}

interface UserInputProps {
  dispatchChat: (action: ChatAction) => void;
  chat: ChatState;
  engine: string;
  organiztion: string;
  apiKey: string;
}

const UserInput = ({ dispatchChat, chat, engine, organiztion, apiKey }: UserInputProps) => {
  const [message, setMessage] = React.useState('');
  const sendMessage = async () => {
    if (!organiztion || !apiKey) {
      alert('Please enter your organization and api key');
      return;
    }
    const newMessage: Message = {
      text: message,
      author: Author.HUMAN,
    };
    dispatchChat({
      type: ChatActionType.ADD_MESSAGE,
      payload: newMessage,
    });
    setMessage('');
    dispatchChat({
      type: ChatActionType.ROBOT_RESPONSE_REQUESTED,
      payload: null,
    });
    const response = await getRobotResponse([...chat.messages, newMessage], engine, organiztion, apiKey);
    dispatchChat({
      type: ChatActionType.ADD_MESSAGE,
      payload: response,
    });
  }

  return (
    <div className="UserInput">
      <textarea
        id="message-input"
        placeholder="Type a message..."
        value={message}
        onChange={(e) => setMessage(e.target.value)}
        onKeyPress={(e) => {
          if (e.key === 'Enter') {
            sendMessage();
            e.preventDefault();
          }
        }}
      />
    </div>
  )
}



export default App;
